import React from "react";
import SportsClickList from "./SportsClickList";

export default ({ currentCompetition, currentCountry, competitionEvents, handleClick, myMarkets, currentSportId, updateMyMarkets }) => {
    const updateMyMarketEvents = (marketItemSaved, id, name, sportId) => updateMyMarkets(marketItemSaved ? 'sub' : 'add', id, name, sportId, "Event", currentCountry)
    return (
        <SportsClickList
            currentItem={
                currentCompetition !== undefined
                    ? currentCompetition.name
                    : currentCountry
            }
            newArray={competitionEvents}
            currentItemName={
                currentCompetition !== undefined
                    ? "currentCompetition"
                    : "currentCountry"
            }
            newArrayName={"competitionEvents"}
            listSelector={"name"}
            reverseClickHandler={handleClick}
            clickHandler={data =>
                handleClick(
                    data,
                    "currentEvent",
                    "eventMarkets",
                    "list-markets",
                    "",
                    "",
                    "",
                    data.id,
                    data => data.result
                )
            }
            myMarkets={myMarkets}
            idSelector={"id"}
            currentSportId={currentSportId}
            currentItemFull={
                currentCompetition !== undefined
                    ? currentCompetition
                    : currentCountry
            }
            updateMyMarkets = {updateMyMarketEvents}
        />
    )
};